.card-container{
    display: flex;
    flex-direction: column;

    // width: 31.5%; //max-width: 397px
    // min-width: 310px;
    // // height: 325px;
    // height: calc(0.81864%*width);
    // margin: 0 10px 30px 10px;
    width: 100%;
    height: 100%;

    .card-thumbnail{
        width: 100%;
        // height: 225px;
        height: calc(0.56675*width);
        .card-thumnail-img{
            width: 100%;
            border-radius: 10px;
        }
    }
    .card-infor{
        width: 100%;
        height: 105px;
        display: flex;
        .card-avtChanel{
            width: 13%;          
            .card-avtChanel-img{
                margin-top: 15%;
                width: 80%;
                border-radius: 30px;
            }
        }
        .card-meta{
            width: 82%;
            display: flex;
            flex-direction: column;
            .card-nameVideo{
                text-align: left;
                font-size: 16px;
                color: black;
                margin: 5px 0;
                font-weight: bold;
                width: 100%;
                overflow: hidden;
                text-overflow: ellipsis;
                display: -webkit-box;
                -webkit-line-clamp: 2;
                -webkit-box-orient: vertical;
            }
            .card-nameChanel{
                text-align: left;
                font-size: 14px;
                color: black;
                margin: 5px 0;
                width: 100%;
            }
            .card-views{
                text-align: left;
                font-size: 14px;
                color: black;
                margin: 0;
            }
        }
        .card-menu{
            width: 5%;
            .card-moreIcon{
                margin-top: 5px;
                width: 5px;
            }
        }
    }
}
@media screen and (max-width: 1188px){
    .card-container{
        width: 100%;
        margin-bottom: 30px;
    }
}
@media screen and (max-width: 678px){
    .card-container{
        width: 100%;
        .card-infor{
            .card-avtChanel{
                .card-avtChanel-img{
                    margin-top: 25%;
                }
            }
        }
    }
}